import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import DraftsIcon from '@material-ui/icons/Drafts';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import Grow from '@material-ui/core/Grow';
import { fade } from '@material-ui/core/styles/colorManipulator';
import MediaCard from './MediaCard';
import {ReviewCard} from './ReviewCard';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
  container: {
    display: 'flex',
  },
  paper: {
    paddingLeft: theme.spacing(1),
    textAlign: "center",
    color: theme.palette.text.secondary
  },
}));

export const  NestedList = ({label}) => {


const itemsCard = [
  {
    cardTitle: "ชื่อ", 
    cardDate: "",
    cardPic: "", 
    cardDetail: "", 
    cardOther: "",
  },{
    cardTitle: "ที่อยู่", 
    cardDate: "",
    cardPic: "", 
    cardDetail: "", 
    cardOther: "",
  },{
    cardTitle: "", 
    cardDate: "",
    cardPic: "", 
    cardDetail: "", 
    cardOther: "",
  },{
    cardTitle: "", 
    cardDate: "",
    cardPic: "", 
    cardDetail: "", 
    cardOther: "",
  },
];

  const classes = useStyles();
  const [open, setOpen] = React.useState(true);

  const handleClick = () => {
    setOpen(!open);
  };

  const [card1, setCard1] = React.useState(false);
  const handleClickCard1 = () => {
    setCard1(!card1);
  };

  const [card2, setCard2] = React.useState(false);
  const handleClickCard2 = () => {
    setCard2(!card2);
  };

  return (
    <div className={classes.container}>
    <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      subheader={
        <ListSubheader component="div" id="nested-list-subheader">
          {label}
        </ListSubheader>
      }
      className={classes.root}
    >
      <ListItem button onClick={handleClickCard2}>
        <ListItemIcon>
          <SendIcon />
        </ListItemIcon>
        <ListItemText primary="ข้อมูลทั่วไป" />
      </ListItem>
      <ListItem button onClick={handleClickCard1}>
        <ListItemIcon>
          <DraftsIcon />
        </ListItemIcon>
        <ListItemText primary="ประวัติการทำงาน" />
      </ListItem>
      <ListItem button onClick={handleClick}>
        <ListItemIcon>
          <InboxIcon />
        </ListItemIcon>
        <ListItemText primary="ประวัติการศึกษา" />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button className={classes.nested}>
            <ListItemIcon>
              <StarBorder />
            </ListItemIcon>
            <ListItemText primary="Starred" />
          </ListItem>
        </List>
      </Collapse>
    </List>
    { card2 && (
    <div >
    <Grow in={card2}>
          <div elevation={0} className={classes.paper}>
            
        <ReviewCard itemsCard={itemsCard[0]}></ReviewCard>
          </div>
        </Grow>
        </div>

    )}
    { card1 && (
    <div >
    <Grow in={card1}>
          <div elevation={0} className={classes.paper}>
            
        <MediaCard></MediaCard>
          </div>
        </Grow>
        </div>

    )}
      </div>
  );
}